@use "display-structure";
@use "op-spacing";
//@use "op-columns";
@use "op-select";
@use "op-checkbox";
@use "op-radio";
@use "op-toggle";
@use "op-icons";
@use "op-date";
@use "sass:color";
@use "st-media-queries";

/* ---------------------------------
DEFAULT LIGHT COLORS
----------------------------------*/
$base: #f7f7f7;
$accent: #333;
$field-background: $base;
$field-border: color.adjust( $base, $lightness: -20% );
$field-font: color.adjust( $base, $lightness: -55% );
$focus-background: color.adjust( $base, $lightness: 10% );
$focus-border: color.adjust( $field-border, $lightness: -20% );
$focus-font: color.adjust( $field-font, $lightness: -1% );
$selected-background: color.adjust( $base, $lightness: 10% );
$selected-border: color.adjust( $field-border, $lightness: -5% );
$selected-font: $accent;

@use "op-color";
@include op-color.apply-theme-colors($field-background, $field-border, $field-font, $focus-background, $focus-border, $focus-font, $selected-font, $selected-border, $accent);

